import React from  "react" //{useState,useEffect} 
import {useStaticQuery,graphql} from "gatsby"
//import {Link} from "gatsby"
import {Doughnut} from "react-chartjs-2"

const LangData=()=>{
  const tempData= useStaticQuery(graphql`
  query GitData{
    githubData {
      data{
        viewer {
          name
          avatarUrl
          isHireable
          resourcePath
          repositories {
            totalCount
            nodes {
              name
              description
              homepageUrl
              forkCount
              createdAt
              updatedAt
              resourcePath
              languages {
                totalSize
                totalCount
                edges {
                  size
                  node {
                    name
                    color
                  }
                }
              }
  
              stargazers {
                totalCount
            
              }
            }
          }
        }
      }
    }
  }
  `)
  // totalCount
const totalRepo=tempData.githubData.data.viewer.repositories.totalCount
const langName1=[]
const langSize1=[]
for(let i=0;i<totalRepo;i++){
  let langEachRepo=tempData.githubData.data.viewer.repositories.nodes[i].languages.totalCount
  for(let j=0;j<langEachRepo;j++){
    langName1.push(tempData.githubData.data.viewer.repositories.nodes[i].languages.edges[j].node.name)
    langSize1.push(tempData.githubData.data.viewer.repositories.nodes[i].languages.edges[j].size)
  }
}
console.log(langName1);
console.log(langSize1);
// [key,value]
var langSet = [];
for(var i=0;i<langSize1.length;i++){

  langSet.push([langName1[i],langSize1[i]]);
  
}
console.log(langSet);
// 
var sum={};
for(var i=0;i<langSet.length;i++){
  var key=langSet[i][0];     // i++keylangSet[i]
  if(key in sum){            // sumkey
    sum[key]+=langSet[i][1]; // langSet[i]
  }else{
    sum[key]=langSet[i][1];  //
  }
}
console.log(sum);
delete sum['Mask']
delete sum['HLSL']
delete sum['Mathematica']
delete sum['ShaderLab']

const label1=[];
const data1=[];
for(var key in sum){
  label1.push(key);
  data1.push(sum[key])
}
console.log(label1);
console.log(data1);
  const data={
    labels:label1,
    datasets:[{
      backgroundColor:["#178600","#2B7489","#e34c26","#563D7C","#f1e05a"],
      data:data1,
    },],
  };
return(
  <Doughnut data={data} />
)
}

